import { Player } from '../core/player';
import { Card, CardType } from '../core/card';

export class Board {
    private _player: Player;
    private _oponent: Player;

    public constructor() {
        const cards = [
            new Card(3, CardType.heal),
            new Card(1, CardType.shield),
            new Card(1, CardType.attack),
            new Card(3, CardType.attack),
            new Card(2, CardType.shield)
        ]
        this._oponent = new Player(10, 2, cards);
        this._player = new Player(6, 0, cards);
    }

    public playPlayerCard(index: number): void {
        const card = this._player.cards[index];
        if(card.type === CardType.attack) {
            this._oponent.applyEffects(card);
        } else {
            this._player.applyEffects(card)
        }
        this._player.useCard(index);
    }

    public getPlayerCards(): Card[] {
        return this._player.cards;
    }

    public getOponentCards(): Card[] {
        return this._oponent.cards;
    }

    public getPlayerHealth(): number {
        return this._player.health;
    }

    public getOponentHealth(): number {
        return this._oponent.health;
    }

    public getPlayerShield(): number {
        return this._player.shield;
    }

    public getOponentShield(): number {
        return this._oponent.shield;
    }
}
