export enum CardType {
    heal,
    attack,
    shield
}
export class Card {
    private _points: number;
    private _type: CardType;

    public constructor(points: number, type: CardType) {
        this._points = points;
        this._type = type;
    }

    public get points(): number {
        return this._points;
    }

    public get type(): CardType {
        return this._type;
    }

    public randomize(): void {
        this._points = this._getRandomInt(1, 4);
        this._type = this._getRandomCardType();
    }

    private _getRandomInt(min: number, max: number): number {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min)) + min;
    }

    private _getRandomCardType(): CardType {
        const index = this._getRandomInt(0, 3);
        switch (index) {
            case 0:
                return CardType.attack;
            case 1:
                return CardType.heal;
            default:
                return CardType.shield;
        }
    }
}
