import {Card, CardType} from './card';
import { Deck } from './deck';

export class Player {
    private _maxHealth = 10;
    private _maxShield = 2;
    private _health: number;
    private _shield: number;
    private _cards: Card[];
    private _deck: Deck;

    public constructor(health: number, shield: number, cards: Card[]) {
        this._health = health;
        this._shield = shield;
        this._cards = this._cloneCards(cards);
    }

    private _cloneCards(cards: Card[]): Card[] {
        let clones: Card[] = [];
        cards.forEach(function (card): void {
            clones.push(new Card(card.points, card.type));
        });
        return clones;
    }

    public get health(): number {
        return this._health;
    }

    public set health(health: number) {
        this._health = health;
        this._health = Math.min(Math.max(this._health, 0), this._maxHealth);
    }

    public get shield(): number {
        return this._shield;
    }

    public set shield(shield: number) {
        this._shield = shield;
        this._shield = Math.min(Math.max(this._shield, 0), this._maxShield);
    }

    public get cards(): Card[] {
        return this._cards;
    }

    public set cards(cards: Card[]) {
        this._cards = cards;
    }

    public useCard(index: number): void {
        const card = this._cards[index];
        card.randomize(); // Simulate discarding card and drawing a new one.
    }

    public applyEffects(card: Card): void {
        switch(card.type) {
            case CardType.heal: {
                this.health += card.points;
                break;
            }
            case CardType.shield: {
                this.shield += card.points;
                break;
            }
            case CardType.attack: {
                this._receiveAttack(card.points);
                break;
            }
            default: {
                console.log(`Card type: '${card.type}' not found.`);
            }
        }
    }

    private _receiveAttack(points: number): void {
        if (this.shield > 0) {
            this._shield -= points;
        } else {
            this._health -= points;
        }
    }
}
