import { Board } from '../models/board-model';
import { BoardView, CardView } from '../views/board-view';

export class BoardController {
    private _model: Board;
    private _view: BoardView;

    public constructor(model: Board, view: BoardView) {
        this._model = model;
        this._view = view;

        // Bind handlers to view events
        view.setCardClickHandler(this.handleEvent.bind(this));
    }

    public renderBoard(): void {
        let cards = this._model.getPlayerCards();
        let displayCards: CardView[] = [];
        cards.forEach((card): void => {
            displayCards.push({
                points:card.points,
                type: card.type});
        })
        this._view.renderPlayerCards(displayCards);
        cards = this._model.getOponentCards();
        displayCards = [];
        cards.forEach((card): void => {
            displayCards.push({
                points:card.points,
                type: card.type});
        })
        this._view.renderOponentCards(displayCards);
        this._view.renderPlayerHealth(this._model.getPlayerHealth());
        this._view.renderOponentHealth(this._model.getOponentHealth());
        this._view.renderPlayerShield(this._model.getPlayerShield());
        this._view.renderOponentShield(this._model.getOponentShield());
    }

    public handleEvent(event: Event): void {
        event.stopPropagation();
        switch(event.type){
            case "click":
                this.clickHandler(event.currentTarget as HTMLElement);
                break;
            default:
                break;
        }
    }

    private clickHandler(element: HTMLElement): void{
        if (element.id) {
            this._model.playPlayerCard(parseInt(element.id));
            this.renderBoard();
        }
    }
}
