export interface CardView {
    points: number;
    type: number;
}

export class BoardView {
    private _cardTypes = ["‚ù§Ô∏è", "‚öîÔ∏è", "üõ°Ô∏è"];

    public setCardClickHandler(handler: EventListenerObject): void {
        let cards = document.querySelectorAll('#player-cards .card');
        cards.forEach( (element): void => {
            element.addEventListener('click', handler);
        });
    }

    public renderPlayerHealth(health: number): void {
        this._renderHealth(health, document.querySelector(`#player-health`));
    }

    public renderOponentHealth(health: number): void {
        this._renderHealth(health, document.querySelector(`#oponent-health`));
    }

    public renderPlayerCards(cards: CardView[]): void {
        this._renderCards(cards, this._getPlayerCards());
    }

    public renderOponentCards(cards: CardView[]): void {
        this._renderCards(cards, this._getOponentCards());
    }

    public renderPlayerShield(shields: number): void {
        this._renderShields(shields, document.querySelector(`#player-shield`));
    }

    public renderOponentShield(shields: number): void {
        this._renderShields(shields, document.querySelector(`#oponent-shield`));
    }

    private _renderCards(cards: CardView[], nodes: NodeListOf<Element>): void {
        for (let i = 0; i < cards.length; i++){
            nodes[i].innerHTML = this._cardTemplate(
                cards[i].points,
                cards[i].type
            );
        }
    }

    private _renderHealth(health: number, node: Element): void {
        node.innerHTML = this._healthTemplate(health);
    }

    private _renderShields(shields: number, node: Element): void {
        node.innerHTML = this._shieldTemplate(shields);
    }

    private _getPlayerCards(): NodeListOf<Element> {
        return this._getCards('player-cards');
    }

    private _getOponentCards(): NodeListOf<Element> {
        return this._getCards('oponent-cards');
    }

    private _getCards(containerID: string): NodeListOf<Element> {
        return document.querySelectorAll(`#${containerID} .card`);
    }

    private _cardTemplate(
        points: number,
        type: number
    ): string {
        let template = "";
        const cardLabel = points.toString();
        const cardSuit = this._cardTypes[type];
        template = `
            <div class="card-value-top">${cardLabel}</div>
            <div class="card-suit">${cardSuit}</div>
            <div class="card-value-bottom">${cardLabel}</div>
        `
        return template;
    }

    private _healthTemplate(
        health: number
    ): string {
        let template = "";
        for (let i = 0; i < 10; i++) {
            if (health <= i) {
                template += '<div class="health-indicator">üñ§</div>\n';
            } else {
                template += '<div class="health-indicator">‚ù§Ô∏è</div>\n';
            }
        }
        return template;
    }

    private _shieldTemplate(
        shield: number
    ): string {
        let template = "";
        for (let i = 0; i < shield; i++) {
            template += '<div class="shield-indicator">üõ°Ô∏è</div>\n';
        }
        return template;
    }
}
